.place {
  -ms-flex-align: center;
      align-items: center;
  background: #40A7E9;
  color: #fff;
  display: -ms-flexbox;
  display: flex;
  margin: 0 0 20px;
  overflow: hidden;
  position: relative;
}

.place-inner-item {
  -ms-flex: 1 1;
      flex: 1 1;
  padding: 20px;
  position: relative;
}

.place-inner-item:not(:last-child):after {
  content: '';
  background: rgba(242,251,251,0.3);
  height: 300%;
  position: absolute;
  right: 0;
  top: -100%;
  width: 1px;
}

.place-icon {
  border-color: transparent;
  -ms-flex: inherit;
      flex: inherit;
}

.place-name {
  -ms-flex: 2 1;
      flex: 2 1;
}

.place-update {
  border: 1px solid #fff;
  border-radius: 50%;
  cursor: pointer;
  display: block;
  height: 31px;
  margin: 0;
  position: relative;
  width: 31px;
}

.place-remove {
  border: 1px solid #fff;
  border-radius: 50%;
  cursor: pointer;
  display: block;
  height: 31px;
  margin: 0;
  position: relative;
  width: 31px;
}

.place-remove:after,
.place-remove:before {
  background: #fff;
  content: '';
  display: block;
  height: 1px;
  left: 7px;
  position: absolute;
  top: 14px;
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
  width: 15px;
}

.place-remove:after {
  top: 7px;
  left: 14px;
  width: 1px;
  height: 15px;
}

.place-icon {
  font-size: 30px;
}

.place-icon-update {
  -webkit-animation: rotateRefresh 1s infinite linear;
          animation: rotateRefresh 1s infinite linear;
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
  border-color: #fff #fff #fff transparent;
  border-style: solid;
  border-width: 1px;
  border-radius: 50%;
  cursor: pointer;
  display: block;
  height: 31px;
  position: relative;
  -webkit-transform-origin: 50% 50%;
      -ms-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  -webkit-transition: background-color 0.3s ease-in-out;
  -o-transition: background-color 0.3s ease-in-out;
  transition: background-color 0.3s ease-in-out;
  width: 31px;
}

.place-icon-update:hover {
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

.place-icon-update-arrow {
  border-color: transparent #fff transparent;
  border-style: solid;
  border-width: 5px 10px 5px 0;
  left: -3px;
  position: absolute;
  top: 20px;
  -webkit-transform: rotate(25deg);
      -ms-transform: rotate(25deg);
          transform: rotate(25deg);
}

.place-empty {
  font-style: italic;
  text-align: center;
  text-shadow: 0 1px 1px #000;
}

.fade-enter,
.fade-appear {
  opacity: 0;
}

.fade-enter.fade-enter-active,
.fade-appear.fade-appear-active {
  opacity: 1;
  -webkit-transition: opacity 0.5s;
  -o-transition: opacity 0.5s;
  transition: opacity 0.5s;
}

.fade-leave {
  opacity: 1;
}

.fade-leave.fade-leave-active {
  opacity: 0;
  -webkit-transition: opacity 0.3s;
  -o-transition: opacity 0.3s;
  transition: opacity 0.3s;
}

@-webkit-keyframes rotateRefresh {
  from {
    transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes rotateRefresh {
  from {
    transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}



